<template>
    <mu-container>
        <mu-row>
            <mu-col span="6">
                <div class="leftBar">
                    <cvPortrait></cvPortrait> 
                </div>
            </mu-col>
            <mu-col span="10">
                <div class="rightBar">
                    <cvExperience></cvExperience>
                </div>
            </mu-col>
        </mu-row>
    </mu-container>
</template>

<script>
import cvPortrait from '../components/cvPortrait'
import cvExperience from '../components/cvExperience'
export default {
    name: 'cv',
    components: {
        cvPortrait,
        cvExperience
    }
}
</script>
<style scoped>
.leftBar {
    background-color: lightslategray
}
</style>


